.contacts {
  list-style: none;
  padding: 0;

  &--scroll {
    order: 1;
    display: grid;
    grid-column: 1 / 3;
    margin: 0;

    //Tablet
    @media (min-width: 768px) {
      margin-top: 64px;
      padding: 0 57px;
    }
  }
}

.contacts--main {
  display: flex;

  @media (min-width: 768px) {
    display: none;
  }
}

.contacts__item {
  z-index: 1;
  font-family: "Blogger Sans", "Arial", sans-serif;

  &--authorization {
    display: block;
    background-color: $basic-yellow;
    padding: 14px 30px;
    font-size: 17px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    border-radius: 25px;
  }
}

.contacts__item-link {
  display: flex;
  align-items: center;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: $basic-blue-dark;

  //Tablet
  @media (min-width: 768px) {
    font-size: 28px;
  }

  &--authorization {
    justify-content: center;
    justify-self: end;
    background-color: $basic-yellow;
    font-size: 20px;
    padding: 14px 30px;
    border-radius: 25px;
  }

}

.contacts__icon {
  position: relative;
  display: inline-block;
  margin-right: 15px;
  padding: 23px;
  border-radius: 50px;

  //Tablet
  @media (min-width: 768px) {
    margin-right: 23px;
    padding: 34px;
  }

  &--scroll {
    background-color: $basic-blue-dark;
  }

