.site-navigation {
  list-style: none;
  padding: 0;
  margin: 0;

  &--scroll {
    grid-column: 1 / 3;
    display: grid;
    grid-gap: 10px;
    margin-top: 24px;

    //Tablet
    @media (min-width: 768px) {
      grid-gap: 49px;
      padding: 0 57px;
    }
  }
}

.site-navigation__item {
  &--scroll {
    text-align: center;

    //Tablet
    @media (min-width: 768px) {
      text-align: left;
      font-size: 60px;
    }
  }
}

.site-navigation__link {
  font-weight: 500;
  font-size: 20px;
  text-transform: uppercase;
  text-decoration: none;
  color: $white;

  &--scroll {
    font-size: 30px;
    color: $basic-blue-dark;

    //Tablet
    @media (min-width: 768px) {
      font-size: 60px;
    }
  }
}
