.page-header {
  padding: 25px;

  //Tablet
  @media (min-width: 768px) {
    padding: 45px 40px;
  }

  &--scroll {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 52px;
    background-color: $white;

    //Tablet
    @media (min-width: 768px) {
      grid-template-columns: 1fr 1fr 1fr;
      row-gap: 90px;
      padding-bottom: 80px;
    }
  }
}

.page-header__logo {
  display: inline-block;
}

.page-header__button {
  align-self: baseline;
  justify-self: end;

  //Tablet
  @media (min-width: 768px) {
    justify-self: center;
  }
}

.page-header__navigation {
  padding: 25px;

  &--scroll {
    grid-gap: 49px;

    //Tablet
    @media (min-width: 768px) {
      padding: 67px 97px;
    }
  }
}

.page-header__authorization {
  &--scroll {
    grid-column: 1 / 3;

    //Tablet
    @media (min-width: 768px) {
      grid-column: 3;
      grid-row: 1;
    }
  }
}
